/*
 * @Author:  
 * @Date: 2018-06-12 13:26:04 
 * @Last Modified by: 
 * @Last Modified time: 2019-04-04 09:44:15
 */
<template>
  <div>
    <div class="row"
         style="padding:15px 16px;">
      <span class="item discount"></span>
      <span class="item type"></span>
      <span class="item createdate"></span>
      <span class="item operation"></span>
    </div>
    <van-list v-model="loading"
              :finished="finished"
              @load="getTable">
      <van-cell v-for="item in list"
                :key="item.id"
                @click="detailInfo(item)">
        <span class="item discount">{{item.discount}}{{ item.discount=="--"?"":item.mode==2?"":"%"}}</span>
        <span class="item type">
          <template v-if="item.mode === 0">
            
          </template>
          <template v-if="item.mode === 1">
            
          </template>
          <template v-if="item.mode === 2">
            
          </template>
        </span>
        <span class="item createdate">{{item.createdate}}</span>
        <template v-if="item.userflag === 1">
          <span class="item operation usered"></span>
        </template>
        <template v-else-if="item.userflag === 0&&item.isexpiry===0">
          <span class="item operation">
            <van-button size="mini"
                        @click.stop="invalid(item.id)"></van-button>
          </span>
        </template>
        <template v-else-if="item.userflag === 0&&item.isexpiry===1">
          <span class="item operation disabled"></span>
        </template>
        <template v-else-if="item.userflag === 2">
          <span class="item operation disabled"></span>
        </template>
      </van-cell>
    </van-list>
    <van-dialog v-model="show"
                title="">
      <p class="msgInfo">
        <span v-for="(item,index) in dialog.ClassInfo"
              :key="index">{{item.classname}} : {{item.ratio}}%</span>
      </p>
      <p class="msgInfo">
        <span> </span>
        {{dialog.discountcode}}
      </p>
      <p class="msgInfo">
        <span> </span>
        {{dialog.userdate}}
      </p>
      <p class="msgInfo">
        <span> </span>
        {{dialog.c_comname}}
      </p>
      <p class="msgInfo">
        <span> </span>
        {{dialog.retailcode}}
      </p>
    </van-dialog>
  </div>
</template>

<script>
import discountListApi from "@/pages/index/services/discountList";
import util from "@/pages/index/helper/util";
export default {
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      show: false,
      dialog: {}
    };
  },
  methods: {
    // 
    getTable () {
      const params = {
        count: this.list.length + 1,
        company_id: window.localStorage.getItem("company_id")
      };
      discountListApi.getTable({ params: { ...params } }).then(
        res => {
          const { status, message } = res.data;
          if (res.data.status == 0) {
            this.list = this.list.concat(res.data.data);
            this.loading = false;
            if (this.list.length >= res.data.totalcount) {
              this.finished = true;
            }
          } else if (res.data.status == 5) {
            this.$router.push({
              name: "login"
            });
          } else {
            this.$toast(message);
            this.loading = false;
            this.finished = true;
          }
        },
        () => {
          this.$toast(", ");
        }
      );
    },
    // 
    invalid (id) {
      discountListApi
        .invalid({
          data: {
            id,
            company_id: window.localStorage.getItem("company_id")
          }
        })
        .then(
          res => {
            const { status, message } = res.data;
            if (res.data.status == 0) {
              this.list.forEach(item => {
                if (item.id == id) {
                  item.userflag = 2;
                }
              });
            }
            this.$toast(message);
          },
          () => {
            this.$toast(", ");
          }
        );
    },
    // 
    detailInfo (row) {
      if (row.userflag == 1) {
        this.show = true;
        this.dialog = row;
      } else if (row.userflag == 0) {
        const { discountcode, intro, notes, expiry_date, rollname } = row;
        window.location.href = `/qrcode.html?barcode=${discountcode}&intro=${encodeURI(
          intro
        )}&notes=${encodeURI(notes)}&expiry_date=${encodeURI(
          expiry_date
        )}&rollname=${encodeURI(rollname)}`;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.row {
  width: 100%;
  height: 100px;
  box-sizing: border-box;
  font-size: 0;
  background: #51b8cb;
  color: white;
  position: fixed;
  z-index: 1;
}
.van-list {
  padding-top: 100px;
}
.item {
  display: inline-block;
  text-align: center;
  font-size: 30px;
  letter-spacing: 2px;
  .van-button--mini {
    width: 120px;
    height: 50px;
    font-size: 28px;
  }
}
.van-cell {
  font-size: 0;
}
.discount {
  width: 15%;
}
.type {
  width: 20%;
}
.createdate {
  width: 40%;
}
.operation {
  width: 25%;
}
.usered {
  color: #51b8cb;
}
.disabled {
  color: #999;
  text-decoration: line-through;
}
.msgInfo {
  color: #666;
  font-size: 20px;
  padding: 10px;
  span {
    display: inline-block;
    padding-left: 20px;
  }
}
</style>


